.main-loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	background-color: $white;
	transition: opacity 1s, visibility 1s;
	@include vertical;
	&.is-hidden {
		opacity: 0;
		visibility: hidden; }
	&__text {
		position: relative;
		line-height: 1;
		font-size: 200px;
		color: $white;
		font-family: $sofia;
		vertical-align: middle;
		font-weight: 900;
		text-shadow: 0px 20px 69px rgba(78, 73, 122, 0.11);
		// animation: mainLoader 4s ease-out infinite
		opacity: 0;
		visibility: hidden;
		transition: opacity 1s, visibility 1s;
		display: inline-block;
		body.is-loaded & {
			opacity: 1;
			visibility: visible;
			transition-delay: 1s; } } }


@keyframes mainLoader {
	0% {
		opacity: 0; }
	40% {
		opacity: 1; }
	60% {
		opacity: 1; }
	100% {
		opacity: 0; } }
