.works {
	margin: 227px auto;
	overflow: hidden;
	&_recent {
 }		// min-height: 3000px
	&__filter-title {
		position: relative;
		display: inline-block;
		&:after {
			content: '';
			position: absolute;
			right: -30px;
			top: 50%;
			transform: translateY(50%);
			@include arr(10, 6, $red, t); } }
	&__container {
		position: relative;
		opacity: 0;
		visibility: hidden;
		transition: opacity $tr, visibility $tr;
		&.is-init {
			opacity: 1;
			visibility: visible; } }
	&__item {
		width: calc(50% - 60px);
		@include md {
			width: calc(50% - 50px); }
		&:nth-child(2) {
			padding-top: 200px; } }
	// 	position: relative
	// 	.work
 }	// 		+coverdiv

.work {
	// width: 20%
	padding-bottom: 110px;
	&__image-wrap {
		overflow: hidden;
		transition: transform $tr;
		background-color: #ccc;
		backface-visibility: hidden;
		@include hover {
			transform: scale(0.97);
			.work__image {
				transform: scale(1.1); } } }
	&__image {
		transition: transform $tr;
		img {
			max-width: 100%;
			// width: 100%
			display: block; } } }

.js-masonry-width {
	width: calc(50% - 60px);
	@include md {
		width: calc(50% - 50px); } }

.js-masonry-gutter {
	width: 120px;
	@include md {
		width: 100px; } }
