body {
  background: #FFF;
  line-height: 1.4;
  font-family: ProximaNova, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.667;
  color: #817D8E;
}

.inner {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

@media only screen and (max-width: 1152px) {
  .inner {
    max-width: 960px;
  }
}

.title-lg {
  font-family: SofiaPro, sans-serif;
  font-size: 200px;
  line-height: 1;
  font-weight: 900;
  color: #817D8E;
}

.title-lg_light {
  color: rgba(129, 125, 142, 0.1);
}

@media only screen and (max-width: 1152px) {
  .title-lg {
    font-size: 140px;
  }
}

@media only screen and (max-width: 599px) {
  .title-lg {
    font-size: 60px;
  }
}

.title {
  font-family: SofiaPro, sans-serif;
  font-size: 52px;
  line-height: 1.15;
  font-weight: 400;
  line-height: 1;
  color: #3E3852;
}

@media only screen and (max-width: 599px) {
  .title {
    font-size: 28px;
  }
}

.title-sm {
  font-family: SofiaPro, sans-serif;
  font-size: 28px;
  line-height: 1.428;
  color: #3E3852;
}

@media only screen and (max-width: 599px) {
  .title-sm {
    font-size: 18px;
    line-height: 1.5;
  }
}

.text-light {
  color: #9F9CA9;
}

.block {
  display: flex;
}

.main-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #fff;
  transition: opacity 1s, visibility 1s;
  text-align: center;
  font-size: 0;
}

.main-loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.main-loader.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.main-loader__text {
  position: relative;
  line-height: 1;
  font-size: 200px;
  color: #fff;
  font-family: SofiaPro, sans-serif;
  vertical-align: middle;
  font-weight: 900;
  text-shadow: 0px 20px 69px rgba(78, 73, 122, 0.11);
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
  display: inline-block;
}

body.is-loaded .main-loader__text {
  opacity: 1;
  visibility: visible;
  transition-delay: 1s;
}

@keyframes mainLoader {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.works {
  margin: 227px auto;
  overflow: hidden;
}

.works__filter-title {
  position: relative;
  display: inline-block;
}

.works__filter-title:after {
  content: '';
  position: absolute;
  right: -30px;
  top: 50%;
  transform: translateY(50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 6px 5px;
  border-color: transparent transparent #F03454 transparent;
}

.works__container {
  position: relative;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.works__container.is-init {
  opacity: 1;
  visibility: visible;
}

.works__item {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1152px) {
  .works__item {
    width: calc(50% - 50px);
  }
}

.works__item:nth-child(2) {
  padding-top: 200px;
}

.work {
  padding-bottom: 110px;
}

.work__image-wrap {
  overflow: hidden;
  transition: transform 0.3s;
  background-color: #ccc;
  backface-visibility: hidden;
}

.no-touch .work__image-wrap:hover {
  transform: scale(0.97);
}

.no-touch .work__image-wrap:hover .work__image {
  transform: scale(1.1);
}

.work__image {
  transition: transform 0.3s;
}

.work__image img {
  max-width: 100%;
  display: block;
}

.js-masonry-width {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1152px) {
  .js-masonry-width {
    width: calc(50% - 50px);
  }
}

.js-masonry-gutter {
  width: 120px;
}

@media only screen and (max-width: 1152px) {
  .js-masonry-gutter {
    width: 100px;
  }
}

[data-anim-from] {
  position: relative;
}

[data-anim-from].is-animate {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

[data-anim-text-parent] {
  overflow: hidden;
  position: relative;
}

[data-anim-text-from="bottom"] {
  display: inline-block;
  position: relative;
  transform: translateY(102%);
  transition: transform 2s;
}

.is-animate [data-anim-text-from="bottom"] {
  transform: translateY(0);
}

[data-anim-from="bottom"] {
  transition: opacity 1s, visibility 1s, transform 1s;
  transform: translateY(20%);
  opacity: 0;
  visibility: hidden;
}
